export class BackendService {
  async probarConexion(): Promise<{ success: boolean; message: string }> {
    try {
      const response = await fetch('/api/seguridad/fichas');
      
      if (response.ok) {
        return { 
          success: true, 
          message: '✅ Sistema funcionando correctamente' 
        };
      } else {
        return { 
          success: false, 
          message: `❌ Error en el sistema: ${response.status}` 
        };
      }
    } catch (error) {
      return { 
        success: false, 
        message: '❌ No se pudo conectar al sistema' 
      };
    }
  }

  async obtenerFichas(search?: string, estado?: string) {
    const params = new URLSearchParams();
    if (search) params.append('search', search);
    if (estado) params.append('estado', estado);
    
    const queryString = params.toString();
    const url = queryString ? `/api/seguridad/fichas?${queryString}` : '/api/seguridad/fichas';
    
    const response = await fetch(url);
    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.error || 'Error al obtener fichas');
    }
    return response.json();
  }

  // CAMBIAR: number → string
  async obtenerFicha(id: string) {
    const response = await fetch(`/api/seguridad/fichas/${id}`);
    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.error || 'Error al obtener la ficha');
    }
    return response.json();
  }

  async crearFicha(fichaData: any) {
    const response = await fetch('/api/seguridad/fichas', {
      method: 'POST',
      headers: { 
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(fichaData),
    });
    
    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.error || 'Error al crear ficha');
    }
    
    return response.json();
  }

  // ✅ Esta está correcta
  async actualizarFicha(id: string, datos: any): Promise<any> {
    try {
      const response = await fetch(`/api/seguridad/fichas/${id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(datos),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || `Error ${response.status}`);
      }

      return await response.json();
    } catch (error) {
      console.error('❌ Error actualizando ficha:', error);
      throw error;
    }
  }

  // CAMBIAR: number → string
  async eliminarFicha(id: string) {
    const response = await fetch(`/api/seguridad/fichas/${id}`, {
      method: 'DELETE',
    });
    
    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.error || 'Error al eliminar ficha');
    }
    
    return response.json();
  }
}

export const backendService = new BackendService();